import { userService } from "../services";
import { router } from "../helpers";
import { tokenToData } from "../helpers";

const user = JSON.parse(localStorage.getItem("user"));
const initialState = user
  ? { status: { loggedIn: true }, user }
  : { status: {}, user: null };
const userData = tokenToData(user);

export const authentication = {
  namespaced: true,
  state: {
    initialState: initialState,
    userData: userData,
  },
  actions: {
    login({ dispatch, commit }, { username, password }) {
      commit("loginRequest", { username });

      userService.login(username, password).then(
        (user) => {
          if (user.token) {
            commit("loginSuccess", user);
            router.push("/panel");
          } else if (user == "Brak uytkownika") {
            dispatch("alert/error", "User doesn't exist", { root: true });
          } else if (user == "Bdne haso") {
            dispatch("alert/error", "Incorrect password", { root: true });
          } else {
            dispatch("alert/error", user, { root: true });
          }
        },
        (error) => {
          commit("loginFailure", error);
          dispatch("alert/error", error, { root: true });
        }
      );
    },
    register({ dispatch, commit }, { username, password }) {
      commit("loginRequest", { username });

      userService.register(username, password).then(
        (user) => {
          if (user.token) {
            commit("loginSuccess", user);
            this.userData = tokenToData(user);
            router.push("/panel");
          } else if (user == "Nazwa uytkownika zajta") {
            dispatch("alert/error", "This username is already in use", {
              root: true,
            });
          } else {
            dispatch("alert/error", user, { root: true });
          }
        },
        (error) => {
          commit("loginFailure", error);
          dispatch("alert/error", error, { root: true });
        }
      );
    },
    logout({ commit }) {
      userService.logout();
      commit("logout");
    },
  },
  mutations: {
    loginRequest(state, user) {
      state.status = { loggingIn: true };
      state.user = user;
    },
    loginSuccess(state, user) {
      this.userData = tokenToData(user);
      state.user = user;
    },
    loginFailure(state) {
      state.status = {};
      state.user = null;
    },
    logout(state) {
      state.status = {};
      state.user = null;
    },
  },
};
